.container {
    display: flex;
    width: 100%;
    border-radius: 2px;
    box-shadow: 0 1px 1px 0 rgb(0 0 0 / 5%);
}

.container .item_link {
    flex: 1;
    background-color: var(--white-color);
    color: var(--text-color);
    font-size: 1.6rem;
    padding: 15px 0;
    text-align: center;
}

.container .item_link:hover {
    color: var(--primary-color);
}

.container .item_link.active {
    color: var(--primary-color);
    border-bottom: 2px solid var(--primary-color);
}