.content {
    display: flex;
    align-items: center;
    padding: 6px 0;
    overflow: auto;
    transition: all linear 0.4s;
}

.content .img {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.2);
    margin-right: 8px;
    background-color: var(--white-color);
}

.details {
    height: 45px;
    width: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    white-space: nowrap;
}

.details .name {
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.7rem;
}

.details .role {
    font-size: 1.3rem;
    line-height: 1.4rem;
}

.content .controller {
    display: flex;
    align-items: center;
    margin-left: auto;
    margin-right: 4px;
}

.content .controller > i{
    font-size: 1.8rem;
    transform: rotate(180deg);
    cursor: pointer;
}

/* Hide */

.content.hide {
    justify-content: center;
}

.content.hide > .img {
    margin: 0;
}

.content.hide .details,
.content.hide .controller {
    display: none;
}