.container {
    width: 100%;
    position: relative;
    height: 100vh;
}

.section_container {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: var(--sidebar-width);
    transition: left linear 0.3s;
    padding: 20px;
}

.orders_container {
    
}

.section_container.sidebar_off {
    left: 60px;
}

.hero_title_container {
    position: absolute;
    top: 30px;
    left: 30px;
}

.hero_title {
    font-size: 3rem;
    font-weight: bold;
    display: inline-block;
    color: rgba(0, 0, 0, 0.8);
}

.content_container {
    background-color: var(--white-color);
    padding-top: 70px;   
}