.users_table {
    width: 100%;
    border-collapse: collapse;
}

.users_table thead {
    background-color: #6f7bda;
}

.users_table thead tr,
.users_table tbody tr {
    height: 44px;
}

.users_table tbody tr:hover {
    background-color: #f9f9f9;
}

.users_table thead tr th,
.users_table tbody tr td {
    vertical-align: middle;
    padding: 0 12px;
}

.users_table thead tr th:last-child,
.users_table tbody tr td:last-child {
    text-align: center;
}

.users_table thead tr th {
    font-size: 1.6rem;
    color: var(--white-color);
}

.users_table tbody tr td {
    font-size: 1.5rem;
    text-align: center;
}

.users_table .user {
    width: 100%;
    display: inline-flex;
    align-items: center;
    gap: 10px;
}

.users_table .user .user_img {
    width: 25px;
    height: 25px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 50%;
}

.users_table .user .user_name {
    font-size: 1.5rem;
    font-weight: 600;
}

.user_control {
    display: inline-block;
    font-size: 2rem;
    color: rgba(0, 0, 0, 0.6);
    cursor: pointer;
    line-height: 10px;
    padding: 4px 2px;
    line-height: 24px;
}

.users_table .user_status {
    display: inline-block;
    font-size: 1.4rem;
    font-weight: 600;
    color: var(--white-color);
    border-radius: 5px;
    padding: 2px 8px;
}

.users_table .user_status.active {
    background-color: #28a745;
}


.users_table .user_status.disabled {
    background-color: #ff2a2a;
}

.profile_modal {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.2);
}

.profile_modal .profile_container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--white-color);
    width: 80%;
    height: 90vh;
    padding: 20px;
}

.profile_container .header > .hero_title {
    font-size: 2.6rem;
    display: inline-block;
    padding: 20px 0;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.8);
}

.profile_container .body {
    margin-top: 20px;
    overflow-x: hidden;
    overflow-y: scroll;
    height: calc(90vh - 40px - 64px - 70px);
}

.user_profile_container .user_avatar_wrapper {
    position: relative;
    padding-top: 100%;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 50%;
}

.user_profile_container .user_avatar_controller {
    position: absolute;
    bottom: -40px;
    left: 50%;
    transform: translateX(-50%);
}

.user_profile_container .user_avatar_controller .input_file {
    display: none;
}

.user_profile_container .user_informations {
    padding: 0 30px;
}

.user_profile_container .user_informations .info_area {
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 10px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    min-height: 50px;
}

.user_profile_container .user_informations .info_area.mini_item {
    display: inline-flex;
}

.user_profile_container .user_informations .info_area.mini_item + .info_area.mini_item {
    margin-left: 20px;
}

.user_profile_container .user_informations .info_area + .info_area {
    margin-top: 30px;
}

.user_profile_container .user_informations .info_area .label {
    display: inline-block;
    color: rgba(0, 0, 0, 0.7);
    font-size: 1.6rem;
    flex-basis: 120px;
    flex-shrink: 0;
}

.user_profile_container .user_informations .info_area .content {
    font-size: 1.7rem;
}

.user_profile_container .user_informations .info_area .content.user_status_active {
    color: #28a745;
}

.user_profile_container .user_informations .info_area .content.user_status_disabled {
    color: #ff2a2a;
}

.user_profile_container .user_informations .info_area .content_editing {
    border: none;
    outline: none;
    font-size: 1.7rem;
    width: 100%;
}

.info_area_group {
    margin-top: 30px;
    display: flex;
    gap: 20px;
}

.info_area_group .info_area {
    flex: 1;
    margin: 0 !important;
}

.info_area_group + .info_area {
    margin-top: 30px;
}

.profile_modal_controllers {
    position: absolute;
    bottom: 20px;
    right: 50%;
    transform: translate(50%);
}

.profile_modal_controllers button + button {
    margin-left: 20px;
}

.product_adding_pane {
    padding-bottom: 30px;
}

.product_adding_pane button + button {
    margin-left: 12px;
}

.adding_pane_bin {
    background-color: #f55252;
    color: var(--white-color);
    border: none;
}

.modal {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.2);
}

.modal .modal_container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--white-color);
    padding: 20px;
    border-radius: 4px;
    width: 400px;
}

.modal_container .modal_header {
    font-size: 2.2rem;
    padding: 10px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.modal_container .modal_body {
    padding: 20px 0;
}

.modal_body .text_area {
    display: flex;
    align-items: center;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding: 0 10px;
}

.modal_body .text_area + .text_area {
    margin-top: 20px;
}

.modal_body .text_area .label {
    flex-basis: 100px;
    font-size: 1.4rem;
    color: rgba(0, 0, 0, 0.7);
}

.modal_body .text_area .input {
    border: 0;
    outline: none;
    display: block;
    font-size: 1.5rem;
    padding: 8px 0;
}

.modal_container .modal_controller {
    text-align: center;
    margin-top: 20px;
}

.modal_container .modal_controller button + button {
    margin-left: 12px;
}

.errors_container {
    border-radius: 4px;
    border: 1px solid rgb(214, 59, 59);
    padding: 10px;
}

.errors_container li {
    display: block;
    font-size: 1.4rem;
    color: rgb(214, 59, 59);
}

.filter_button {
    margin-left: 6px;
}

.has_dropdown {
    position: relative;
}

.filter_button {
    margin-left: 10px;
}

.dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    display: none;
}

.dropdown.open {
    display: block;
}

.status_list_container {
    background-color: var(--white-color);
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
    color: var(--text-color);
    font-size: 1.4rem;
}

.status_list_container .status_list li{
    display: block;
    cursor: pointer;
}

.status_list_container .status_list li:hover {
    background-color: #fafafa;
}

.status_item_link {
    padding: 4px 10px;
    display: block;
    color: var(--text-color);
}