.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.2);
}

.modal_container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--white-color);
    border-radius: 4px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
    width: 400px;
}

.modal_container .modal_header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 20px;
}

.modal_container .modal_header .modal_image {
    height: 70px;
    width: 70px;
}

.modal_container .modal_header .hero_title {
    font-size: 2.2rem;
    font-weight: bold;
}

.modal_container .modal_header .hero_title.success {
    color: #3bb54a;
}

.modal_container .modal_header .hero_title.failure {
    color: #f47365;
}

.modal_container .modal_body {
    text-align: center;
    font-size: 1.6rem;
}

.modal_container .modal_body .value {
    font-weight: bold;
}

.modal_container .modal_controllers {
    text-align: center;
}

.modal_container .modal_controllers {
    margin: 30px 0 20px;
}

.modal_container .modal_controllers button + button {
    margin-left: 12px;
}