.orders_table {
    border-collapse: collapse;
    width: 100%;
}

.orders_table thead {
    font-size: 1.5rem;
    color: var(--white-color);
    background-color: #6f7bda;
}

.orders_table tbody {
    font-size: 1.4rem;
    text-align: center;
}

.orders_table tr {
    height: 44px;
    font-size: 1.6rem;
}

.order_status {
    display: inline-block;
    font-size: 1.5rem;
    font-weight: 600;
}

.order_status.completed {
    color: #28a745;
}

.order_status.pending {
    color: #ffc107;
}

.order_status.delivering {
    color: rgb(1, 118, 195);
}

.order_status.canceled {
    color: #ff2a2a;
}

.orders_table .order_controller {
    display: inline-block;
    padding: 2px;
    cursor: pointer;
    font-size: 1.7rem;
}

/* Modal */

.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.2);
}

.modal_container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--white-color);
    width: 80%;
    height: 90vh;
    padding: 20px;
}

.modal_container .header .hero_title {
    font-size: 2.4rem;
    text-align: center;
    padding: 10px 0;
    text-transform: capitalize;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}   

.modal_container .body {
    padding: 20px 0;
    overflow-y: scroll;
    max-height: calc(90vh - 115px);
}

.modal_container .body::-webkit-scrollbar {
    display: none;
}

.content_container .text_area {
    display: flex;
    align-items: center;
    height: 44px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding: 0 10px;
}

.content_container .text_area {
    margin-bottom: 30px;
}

.content_container .text_area .text_label {
    flex-basis: 110px;
    font-size: 1.5rem;
    color: rgba(0, 0, 0, 0.6);
    font-weight: 600;
}

.content_container .text_area .text_content {
    font-size: 1.5rem;
}

.products_table {
    border-collapse: collapse;
    width: 100%;
}

.products_table thead {
    background-color: var(--primary-color);
    color: var(--white-color);
    font-size: 1.5rem;
    height: 44px;
}

.products_table tbody tr {

}

.products_table tbody tr:nth-child(2n + 1) {
    background-color: #fafafa;
}

.products_table tbody tr:last-child {
    background-color: var(--white-color);
}

.products_table tbody tr:last-child td:first-child {
    text-align: right;
    font-size: 1.6rem;
}

.products_table tbody tr:last-child td:last-child {
    font-size: 1.6rem;
    color: var(--primary-color);
    font-weight: 600;
}

.products_table tbody tr td {
    vertical-align: middle;
    text-align: center;
    font-size: 1.5rem;
    padding: 8px;
}

.product_item {
    display: flex;
    align-items: center;
    width: 350px;
}

.product_item .product_image_wrapper {
    width: 65px;
    height: 65px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid rgba(0, 0, 0, 0.1);
}

.product_item .product_image_wrapper .product_image {
    width: 100%;
}

.product_item .product_name {
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    flex-basis: 50%;
}

.order_controller_container {
    margin-top: 20px;
    display: flex;
    align-items: center;
    gap: 20px;
    font-size: 1.7rem;
}

.modal_controllers {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
}

.order_controller_container .order_controller + .order_controller {
    margin-left: 8px;
}

.has_dropdown {
    position: relative;
}

.filter_button {
    margin-left: 10px;
}

.dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    display: none;
}

.dropdown.open {
    display: block;
}

.status_list_container {
    background-color: var(--white-color);
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
    color: var(--text-color);
    font-size: 1.4rem;
}

.status_list_container .status_list li{
    display: block;
    cursor: pointer;
}

.status_list_container .status_list li:hover {
    background-color: #fafafa;
}

.status_item_link {
    padding: 4px 10px;
    display: block;
    color: var(--text-color);
}