.section_container .adding {
    padding-bottom: 30px;
}

.section_container .adding button + button {
    margin-left: 12px;
}

.section_container .content_container {
    width: 100%;
}

.content_container .category_table {
    width: 100%;
    border-collapse: collapse;
}

.category_table thead {
    color: var(--white-color);
    background-color: #6f7bda;
}

.category_table thead tr th{
    height: 44px;
    vertical-align: middle;
    font-size: 1.5rem;
    text-align: left;
    padding: 0 12px;
}


.category_table tbody tr td {
    font-size: 1.5rem;
    vertical-align: middle;
    height: 50px;
    padding: 0 12px;
}

.category_table tbody tr td:first-child {
    font-weight: 600;
}

.category_table tbody tr + tr {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.category_table .category_controller {
    font-size: 1.7rem;
    cursor: pointer;
}

.category_control_container {
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: center;
}

.category_control_container .category_control_button {
    display: inline-block;
    font-size: 1.4rem;
    line-height: 1.5rem;
    padding: 4px 12px;
    color: var(--white-color);
    cursor: pointer;
}

.category_control_container .category_control_button.edit {
    background-color: rgb(1, 118, 195);
}

.category_control_container .category_control_button.delete {
    background-color: #ff2a2a;
}

.modal {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.2);
}

.modal .modal_container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--white-color);
    padding: 20px;
    border-radius: 4px;
    width: 400px;
    height: 270px;
}

.modal_container .modal_header {
    font-size: 2.2rem;
    padding: 10px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.modal_container .modal_body {
    padding: 20px 0;
}

.modal_body .text_area {
    display: flex;
    align-items: center;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding: 0 10px;
}

.modal_body .text_area + .text_area {
    margin-top: 20px;
}

.modal_body .text_area .label {
    flex-basis: 100px;
    font-size: 1.4rem;
    color: rgba(0, 0, 0, 0.7);
}

.modal_body .text_area .input {
    border: 0;
    outline: none;
    display: block;
    font-size: 1.5rem;
    padding: 8px 0;
}

.modal_container .modal_controller {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
}

.modal_container .modal_controller button + button {
    margin-left: 12px;
}

.storage_button {
    border: none;
    background-color: #f55252;
    color: var(--white-color);
}