/* Navigation */

.nav {
    z-index: var(--header);
}

.nav-top {
    height: var(--nav-top-height);
}

.nav .nav-item {
    color: var(--white-color);
    font-size: var(--text-size);
}

.nav-item-container > ul,
.nav-item-container > li{
    display: flex;
    align-items: center;
    height: 34px;
}

.nav .nav-link {
    transition: all linear .15s;
    color: var(--white-color);
}

.nav .nav-link-icon {
    margin-right: 4px;
}

.nav .nav-brand-icon{
    margin-left: 10px;
}

.nav .nav-link:hover {
    color: #ffd700;
}

.nav .nav-item-container > ul > li + li {
    margin-left: 18px;
}

.nav-item-separate {
    position: relative;
}

.nav-item-separate::after {
    content: "";
    position: absolute;
    width: 1px;
    height: 75%;
    border-left: 1px solid #FB9086;
    top: 50%;
    left: -9px;
    transform: translateY(-50%);
}

/* nav-search */
.nav-container {
    margin: 10px 0 16px 0;
    height: calc(var(--header-search-height) + 10px);
    display: flex;
    justify-content: space-between;
}

.nav-logo-link {
    display: inline-block;
    padding-right: 50px;
}

.nav-logo {
    height: calc(var(--header-search-height) + 10px);
}

.nav__search-container {
    position: relative;
    flex: 1;
}

.nav__search-box {
    display: flex;
    align-items: center;
    width: 100%;
    height: var(--header-search-height);
    padding: 4px;
    border-radius: 4px;
    background-color: var(--white-color);
}

.nav__search-input-wrapper {
    display: inline-block;
    width: 100%;
    margin-right: 4px;
    position: relative;
}

.nav__search-input {
    width: 100%;
    outline: none;
    border: none;
    border-right: 1px solid var(--primary-color);
    height: calc(var(--header-search-height) - 6px);
    padding: 0 10px;
    font-size: 1.4rem;
}

.nav__search-btn {
    height: 36px;
    line-height: calc(36px - 8px);
}

/* top trending search key */
.nav__search-trending-container {
    position: absolute;
    right: 0;
    left: 0;
    bottom: -14px;
}

.nav__search-trending-key {
    font-size: 1.3rem;
    color: var(--white-color);
    margin-right: 12px;
}

/* Navigation Cartlist */

.nav__cartlist {
    position: relative;
    display: inline-block;
    margin-left: 30px;
}

.nav__cartlist .nav__cartlist-icon {
    color: var(--white-color);
    font-size: 2.2rem;
    padding: 9px 20px;
}

.nav__cartlist .cart_number_badge {
    position: absolute;
    top: 0;
    right: 0;
    background-color: var(--white-color);
    padding: 1px 8px;
    border-radius: 10px;
    border: 1px solid var(--primary-color);
    line-height: 1.4rem;
    font-size: 1.4rem;
    color: var(--primary-color);
    font-weight: 600;
}

.nav__cartlist-box{
    background-color: var(--white-color);
    border-radius: 4px;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
    position: absolute;
    right: 6px;
    top: calc(100% + 2px);
    width: 380px;
    min-height: 294px;
    display: none;
    z-index: var(--popup);
}

.nav__cartlist:hover .nav__cartlist-box {
    display: block;
}

.nav__cartlist-box::after {
    position: absolute;
    content: "";
    border-style: solid;
    border-width: 10px 12px;
    border-color: transparent transparent var(--white-color) transparent;
    top: -20px;
    right: 12px;
}

.nav__cartlist-box::before {
    position: absolute;
    content: "";
    height: 10px;
    width: 60px;
    top: -10px;
    right: 0;
    background-color: transparent;
}

.nav__cartlist-box .nav__cartlist-box-title p{
    padding: 5px 10px;
    font-size: 1.5rem;
    opacity: 0.6;
}

.cartlist-item-link {
    display: block;
}

.cartlist-item-link:hover {
    background-color: var(--body-background-color);
}

.nav__cartlist-item-container {
    overflow-y: scroll;
    min-height: 210px;
    max-height: 310px;
}

.nav__cartlist-item-container::-webkit-scrollbar {
    display: none;
    /* Hide scrollbar for IE, Edge and Firefox */
}
  
.nav__cartlist-item-container {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

.cartlist__item {
    padding: 10px;
    overflow: auto;
}

.cartlist__item .carlist__item-header {
    float: left;
    width: 42px;
    height: 42px;
    border: 1px solid rgba(0, 0, 0, 0.2);
}

.cartlist__item-body {
    float: left;
    display: flex;
    width: calc(100% - 42px);
    padding-left: 10px;
}

.cartlist__item-name-wrapper {
    width: 70%;
    display: inline-block;
}

.cartlist__item-name-wrapper span {
    overflow: hidden;
    width: 80%;
    text-align: left;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 1.4rem;
    color: var(--text-color);
}

.cartlist__item-price-wrapper {
    width: 20%;
    display: inline-block;
}

.cartlist__item-price-wrapper span {
    font-size: 1.4rem;
    color: var(--primary-color);
    position: relative;
}

.cartlist__item-price-wrapper span::after {
    position: absolute;
    content: '\20AB';
    font-size: 1.5rem;
    top: -6px;
    left: -8px;
}

/* Cartlist Control */
.nav__carlist-control {
    float: right;
    padding: 10px 10px 10px 0;
}

/* Carlist Empty */

.nav__cartlist-empty {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    display: flex;
    flex-direction: column;
}

.nav__cartlist-empty img {
    width: 120px;
}

.nav__cartlist-empty span {
    text-align: center;
    font-size: 1.6rem;
}

/* QR dropdown*/

.tab-pane__qr-img {
    width: 170px;
}

.sub-img-container {
    padding: 0 10px;
}

.sub-img-container img{
    display: inline-block;
    width: 75px;
    padding: 0 2.5px;
}

.has-dropdown:hover .dropdown-pane {
    display: block;
}

.tab-pane__qr {
    padding: 5px;
    background-color: var(--white-color);
    border-radius: 4px;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
    top: calc(100% + 10px);
    z-index: var(--popup);
}

.tab-pane__qr::after {
    content: "";
    position: absolute;
    width: 75px;
    height: 10px;
    top: -10px;
    left: 0;
}

.notification__content {
    position: relative;
    height: 306px;
}

.notification__content--empty {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
}


.notification__empty-img {
    width: 15em;
}

.notification__empty-content {
    display: inline-block;
    font-size: 1.6rem;
    text-align: center;
}
/* Notìication dropdown */

.tab-pane__notification {
    background-color: var(--white-color);
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
    width: 350px;
    min-height: 350px;
    position: absolute;
    top: calc(100% + 10px);
    right: 0;
    z-index: var(--popup);
}

.tab-pane__notification::after {
    content: "";
    position: absolute;
    right: 18px;
    bottom: 100%;
    z-index: 2;
    border-style: solid;
    border-width: 12px 14px;
    border-color: transparent transparent var(--white-color) transparent;
}

.tab-pane__notification::before {
    content: "";
    position: absolute;
    top: -14px;
    right: 0;
    width: 62px;
    height: 14px;
}

/* Notification controls */
.notification__control {
    display: flex;
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
}

.notification__control-button {
    display: inline-block;
    flex: 1;
    background-color: var(--body-background-color);
    text-align: center;
    font-size: 1.4rem;
    padding: 10px 0;
}

.notification__control-button:hover {
    color: var(--primary-color);
    background-color: rgba(0, 0, 0, 0.1);    
}

/* Footer */

footer {
    background-color: var(--white-color);
    padding: 30px 0;
}

.footer__item-container li {
    display: block;
}

.footer__item-container .footer__item{
    color: var(--text-color);
    font-size: 1.4rem;
}

.footer__item-container .footer__item-link:hover {
    color: var(--primary-color);
}

.footer__item-container .footer__item-title {
    font-size: 1.5rem;
    font-weight: 700;
    color: rgba(0, 0, 0, 0.54);
    margin-bottom: 10px;
}

/* Footer payment */
.footer__img-container ul{
    display: flex;
    flex-wrap: wrap;
}

.footer__img-container .footer__img {
    width: 55px;
}

.footer__img-container ul li {
    width: 55px;
    height: 55px;
    margin: 10px 10px 0 0;
}

.footer__img-container .footer__img-downloader {
    width: 100%;
}

.footer__qr-wrapper {
    border: 1px solid rgba(0, 0, 0, 0.54);
    padding: 4px;
}

.footer__qr-downloader {
    width: 100%;
}

.footer_icon {
    font-size: 1.6rem;
}

.footer__item-link > span{
    display: inline-block;
    width: 16px;
    margin-right: 4px;
}

/* LoginSite */

.loginsite__background {
    background-image: url('../../../public//resources/img/login_background.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    height: 100vh;
    position: relative;
}

.loginsite__box {
    background-color: var(--white-color);
    border-radius: 4px;
    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1);
    position: absolute;
    display: inline-block;
    min-width: 400px;
    max-width: 425px;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    padding: 30px;
}

.loginsite__box .loginsite__title {
    font-size: 2.6rem;
    margin-bottom: 30px;
    float: left;
}

.loginsite__box .loginsite__swap-button-wrapper {
    float: right;
}

.loginsite__input-container .loginsite__item .loginsite__input{
    outline: none;
    border: none;
    padding: 4px 0;
    font-size: 1.7rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    width: 100%;
}

.loginsite__input-container .loginsite__item + .loginsite__item {
    margin-top: 22px;
}

.loginsite__control-icon {
    width: 25px;
}

.loginsite__confirm-control {
    margin-top: 25px;
}

.loginsite__confirm-control button {
    border: none;
    width: 100%;
    cursor: pointer;
    height: 34px;
}

.loginsite__control-container {
    display: flex;
    justify-content: space-around;
}

.loginsite__control-button {
    border: 1px solid rgba(0, 0, 0, 0.26);
    border-radius: 2px;
    display: inline-block;
    min-width: 116px;
    height: 40px;
    background-color: var(--white-color);
    display: flex;
    align-items: center;
    padding: 0 20px;
}

.loginsite__control-button:hover {
    background-color: rgba(0,0,0,.02);
}

.loginsite__control-button img {
    margin-right: 8px;
}

.horizontal {
    margin: 25px 0;
    font-size: 1.7rem;
    text-align: center;
    color: rgba(0, 0, 0, 0.25);
    position: relative;
}

.horizontal::after {
    content: "";
    position: absolute;
    background-color: rgba(0, 0, 0, 0.25);
    width: 40%;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    height: 0.5px;
}

.horizontal::before {
    content: "";
    position: absolute;
    background-color: rgba(0, 0, 0, 0.25);
    width: 40%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    height: 0.5px;
}

.loginsite__policities {
    margin-top: 15px;
    padding: 0 35px;
    font-size: 1.4rem;
    text-align: center;
    line-height: 1.8rem;
}

.loginsite__policities a {
    color: var(--primary-color);
}

.loginsite__route-back-wrapper {
    position: absolute;
    top: 20px;
    right: 100%;
    background: var(--background-gradient-color);
    opacity: 0.5;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
    box-shadow: -6px 2px 8px rgba(0, 0, 0, 0.1);
    min-width: 20px;
    min-height: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all ease-out .4s;
}

.loginsite__route-back-icon {
    color: var(--white-color);
    font-size: 1.4rem;
}

.loginsite__route-back-wrapper:hover {
    padding: 0 20px;
    opacity: 1;
}


.loginsite__route-back-wrapper:hover .loginsite__route-back-icon {
    display: none;
}

.loginsite__route-back-wrapper:hover .loginsite__route-back-button {
    display: inline-block;
}

.loginsite__route-back-button {
    font-size: 1.4rem;
    color: var(--white-color);
    white-space: nowrap;
    display: none;
}

/* Side Category List */

.sidecategory__container {
    padding: 15px 0;
}

.sidecategory__header{
    font-size: 1.6em;
    margin-bottom: 15px;
}

.sidecategory__header i {
    margin-right: 6px;
    font-size: 1.4rem;
}

.sidecategory__body {
    padding-top: 15px;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.sidecategory__body ul li {
    display: block;
}

.sidecategory__item-link {
    display: inline-block;
    font-size: 1.4rem;
    color: var(--text-color);
    margin-bottom: 8px;
    text-transform: capitalize;
    cursor: pointer;
}

.sidecategory__item-link:hover {
    color: var(--primary-color);
}

.sidecategory__item-link.current {
    color: var(--primary-color);
    position: relative;
}

.sidecategory__current-icon {
    position: absolute;
    font-size: 1.4rem;
    top: 50%;
    left: -10px;
    transform: translateY(-50%);
}

/* Side Filter */

.sidefilter__tittle {
    margin-right: 12px;
}

.sidefilter__tittle p {
    font-size: 1.5rem;
}

.sidefilter__controllers {
    background-color: #EDEDED;
    padding: 13px 20px;
    border-radius: 3px;
    display: flex;
    align-items: center;
}

.sidefilter__filter-button {
    width: 90px;
    padding: 6px 12px;
    box-shadow: 0 1px 1px 0 rgb(0 0 0 / 2%);
}

.sidefilter__filter-button {
    margin-right: 12px;
}

.sidefilter__filter-select-box {
    position: relative;
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    padding: 6px 12px;
    width: 180px;
    font-size: 1.5rem;
    background-color: var(--white-color);
}

.sidefilter__filter-select-box:hover .sidefilter__filter-select-wrapper {
    display: block;
}

.sidefilter__filter-select-wrapper {
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    background-color: var(--white-color);
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
    display: none;
    z-index: var(--mini-tab);
}

.sidefilter__filter-select-wrapper .sidefilter__filter-selector {
    padding: 8px 12px;
    cursor: pointer;
}

.sidefilter__filter-select-wrapper .sidefilter__filter-selector:hover {
    color: var(--primary-color);
}

.view-item-container {
    min-height: 700px;
}

.error_container {
    margin-top: 20px;
    font-size: 1.5rem;
    color: #ff2a2a;
    border-radius: 4px;
    border: 1px solid #ff2a2a;
    background-color: #fadfdf;
    padding: 10px;
}

.error_container li {
    display: block;
    padding: 2px 0;
}