.item_link {
    display: block;
    padding-top: 56.65%;
    background-position: 50%;
}

.item_name_wrapper {
    margin-top: 14px;
    display: flex;
    justify-content: center;
}

.item_name {
    width: 80%;
    display: -webkit-box;
    font-size: 1.5rem;
    line-height: 1.55rem;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    color: var(--text-color);
    text-align: center;
}