.section_container {
    width: 100%;
}

.products_table {
    width: 100%;
}


.products_table tbody {
    font-size: 1.5rem;
}

.products_table thead {
    background-color: #6f7bda;
    color: var(--white-color);
    height: 40px;
    font-size: 1.4rem;
}

.products_table thead tr {

}

.products_table tr {

}

.products_table tbody tr {
    height: 120px;
}  

.products_table tbody tr + tr {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.products_table tbody tr td{
    vertical-align: middle;
}



.products_table tbody tr td::-webkit-scrollbar {
    display: none;
}

.products_table tr th,
.products_table tr td {
    padding: 0 12px;
    text-align: center;
}

.img_wrapper {
    padding-top: 100%;
    width: 100px;
    background-position: 50%;
}

.text {
    display: inline-block;
    max-height: 100px;
    width: 180px;
    overflow-wrap: break-word;
    overflow-y: scroll;
    text-align: left;
}

.text::-webkit-scrollbar {
    display: none;
}

.product_variant_container {
    text-align: left;
    width: 100px;
}

.product_control {
    position: relative;
}

.product_control_icon {
    cursor: pointer;
    font-size: 2rem;
    padding: 2px;
}

.options_container {
    position: absolute;
    background-color: var(--white-color);
    /* display: none; */
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.05);
    padding: 5px;
    top: -10%;
    right: 60%;
    display: none;
}

.options_container.open {
    display: block;
}

.options_container .option {
    font-size: 1.4rem;
    color: var(--white-color);
    border-radius: 5px;
    padding: 0 12px;
    cursor: pointer;
    font-weight: 600;
    white-space: nowrap;
}

.options_container .option + .option {
    margin-top: 5px;
}


.options_container .option.edit {
    background-color: rgb(1, 118, 195);
}

.options_container .option.delete {
    background-color: #ff2a2a;
}

.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.2);
}

.modal_container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--white-color);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    min-width: 960px;
    height: 90vh;
}

.modal_header .hero_title {
    text-align: center;
    font-size: 2.5rem;
    font-weight: 600;
    text-transform: uppercase;
    color: rgba(0, 0, 0, 0.85);
    padding: 20px;
    border-bottom: 2px solid rgba(0, 0, 0, 0.2);
}

.modal_body {
    height: calc(90vh - 66px - 69px);
    padding: 20px;
    overflow-y: scroll;
}

.modal_body::-webkit-scrollbar {
    display: none;
}

.input_group {

}

.input_group.sub_group {
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 30px;
}

.input_group.sub_group .sub_item {
    margin: 30px 0 0 0;
    flex: 1;
}

.input_area {
    padding: 10px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    position: relative;
    min-height: 42px;
}

.input_area + .input_area {
    margin-top: 30px;
}

.input_area .input_wrapper {
    width: 100%;
    min-height: 22px;
}

.input_area .input {
    border: none;
    outline: none;
    width: 100%;
    height: 100%;
    font-size: 1.4rem;
    color: rgba(0, 0, 0, 0.75);
    line-height: 2rem;
}

.input_area > .label {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transform: all .3s;
    font-size: 1.4rem;
}

.input_area > .label.focus {
    top: 0;
    background-color: var(--white-color);
    padding: 0 4px;
    font-size: 1.2rem;
    line-height: 1.2rem;
    color: rgba(0, 0, 0, 0.6);
}

.image_preview {
    padding-top: 65%;
    position: relative;
    border: 1px solid rgba(0, 0, 0, 0.24);
}

.image_preview .image {
    position: absolute;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.input_file_area {
    margin: 12px 0 0 12px;
    position: relative;
    display: inline-block;
    width: 70%;
    border: 1px solid rgba(0, 0, 0, 0.2);
    height: 34px;
    overflow: hidden;
    border-radius: 4px;
}

.input_file {
    position: absolute;
    display: block;
    width: 100%;
    height: 34px;
    line-height: 34px;
    padding: 0 90px 0 12px;
}

.input_file::-webkit-file-upload-button {
    display: none;
}

.input_file_box {
    float: right;
    font-size: 1.5rem;
    color: rgba(0, 0, 0, 0.8);
    font-weight: 700;
    padding: 4px 10px;
    background-color: #ddd;
}

.models_table {
    margin-top: 30px;
    border-collapse: collapse;
    width: 100%;
}

.models_table caption {
    font-size: 2rem;
    color: rgba(0, 0, 0, 0.9);
    text-transform: capitalize;
    padding: 5px 0;
    text-align: left;
}

.models_table thead,
.models_table tbody tr {
    height: 40px;
}

.models_table thead th:first-child,
.models_table tbody td:first-child {
    width: 20%;
}

.models_table thead th {
    font-size: 1.6rem;
    font-weight: normal;
    border: 1px solid rgba(0, 0, 0, 0.2);
}

.models_table tbody td {
    border: 1px solid rgba(0, 0, 0, 0.2);
    padding: 8px;
}

.models_table tbody td:first-child {
    font-size: 1.5rem;
    text-align: center;
}

.model_item {
    position: relative;
    margin: 0px 8px 8px 0px;
}

.model_controllers {
    position: absolute;
    top: 0;
    right: 0;
}

.model_controllers button {
    background-color: var(--white-color);
    outline: none;
    border: 1px solid rgba(0, 0, 0, 0.2);
    width: 20px;
    height: 20px;
}

.popup {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.24);
}

.popup_container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 400px;
    background-color: var(--white-color);
    border-radius: 4px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1);
    padding: 10px;
}


.popup_container .title {
    text-align: center;
    font-size: 1.8rem;
    text-transform: uppercase;
    padding: 5px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.popup_container .content {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.popup_container .content .product_model_name {
    font-size: 1.5rem;
}

.popup_container .content .product_model_input {
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.2);
    font-size: 1.5rem;
    height: 30px;
    line-height: 30px;
    outline: none;
    padding: 0 10px;
}

.popup_container .product_model_controllers {
    margin-top: 30px;
    float: right;
}

.popup_container .product_model_controllers button + button{
    margin-left: 10px;
}

.model_item {
    display: inline-block;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 2px;
    padding: 2px 24px 2px 6px;
    font-size: 1.4rem;
    min-width: 60px;
    min-height: 30px;
    text-align: center;
    cursor: pointer;
    position: relative;
}

.model_item .model_controller {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    color: #ff2a2a;
    cursor: pointer;
    font-size: 2rem;
    padding: 0px 3px;
    line-height: 28px;
    border-left: 1px solid rgba(0, 0, 0, 0.2);
}

.model_item .model_controller:hover {
    background-color: #eee;
}

.add_model {
    display: flex;
    justify-content: center;
}

.add_model_item {
    display: inline-block;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 2px;
    padding: 2px;
    font-size: 1.4rem;
    min-width: 60px;
    min-height: 30px;
    text-align: center;
    cursor: pointer;
}

.model_action {
    color: rgba(0, 0, 0, 0.6);
    font-size: 1.5rem;
    display: block;
    padding: 4px;
    text-align: center;
    cursor: pointer;
}

.model_action:hover {
    color: #ff2a2a;
}

.modal_controllers {
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
    position: absolute;
    padding: 20px;
}

.modal_controllers button + button {
    margin-left: 10px;
}

.description_section {
    margin-top: 40px;
}

.description_container > .title {
    font-size: 2rem;
    color: rgba(0, 0, 0, 0.9);
    text-transform: capitalize;
    padding: 5px 0;
}

.description_section .description_container .content_container .input {
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    padding: 10px;
    font-size: 1.4rem;
    outline: none;
}

.product_adding_pane {
    padding-bottom: 30px;
}

.product_adding_pane button + button {
    margin-left: 12px;
}

.adding_pane_bin {
    background-color: #f55252;
    color: var(--white-color);
    border: none;
}