.container {
    width: 100%;
}

.container .avatar_container {
    display: inline-flex;
    width: 100%;
    align-items: center;
    padding: 10px 0;
}

.avatar_container .avatar_wrapper {
    margin-right: 10px;
}

.avatar_container .avatar_wrapper img {
    width: 45px;
    height: 45px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
}

.avatar_container .name_wrapper .name {
    font-size: 1.5rem;
    font-weight: 600;
}

.user_nav_container {
    padding: 25px 0;
    font-size: 1.5rem;
}

.user_nav_container .nav_item_link {
    color: var(--text-color);
    text-transform: capitalize;
    margin: 10px 0;
}

.user_nav_container .nav_item_link:hover .nav_item_name {
    color: var(--primary-color);
}

.user_nav_container .nav_item_link .nav_logo {
    width: 30px;
    display: inline-block;
    font-size: 1.8rem;
}

.subnav_container {
    padding-left: 30px;
}

.subnav_container .nav_item_link {
    display: block;
}

.horizontal {
    height: 1px;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}