.table {
    border-collapse: collapse;
    display: block;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    overflow: hidden;
}


.table thead,
.table tbody {
    display: block;
}

.table tbody {
    overflow: scroll;
    height: calc(52px*5);
}

.table tbody::-webkit-scrollbar {
    display: none;
}

.table thead {
    background-color: #6f7bda;
    color: var(--white-color);
}

.table thead th {
    font-weight: 600;
}

.table thead th,
.table tbody td {
    font-size: 1.6rem;
    padding: 8px 15px;
}

.table tbody tr {
    cursor: pointer;
}

.table tbody tr:hover {
    background-color: rgba(0, 0, 0, 0.05)
}

.table tr {
    display: inline-flex;
    width: 100%;
}

.table thead th,
.table tbody td {
    flex: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 52px;
}

.table thead th:first-child,
.table tbody td:first-child {
    justify-content: flex-start;
    flex: 6;
}

.table tbody td {
    font-size: 1.6rem;
}

.user {
    display: flex;
    align-items: center;
    gap: 10px;
}

.user .avatar {
    width: 35px;
    height: 35px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 50%;
}

.user .name {

}

.order_status {
    display: inline-block;
    font-size: 1.5rem;
    font-weight: 600;
}

.order_status.completed {
    color: #28a745;
}

.order_status.pending {
    color: #ffc107;
}

.order_status.delivering {
    color: rgb(1, 118, 195);
}

.order_status.canceled {
    color: #ff2a2a;
}