.container {
    width: 100%;
}

.container .header_container {
    width: 100%;
}

.container .body_container {
    margin-top: 30px;
}

.invoice {
    background-color: var(--white-color);
}

.invoice + .invoice {
    margin-top: 20px;
}

.invoice .header {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 20px;
}

.invoice .header .title{
    font-size: 1.7rem;
    font-weight: 600;
}

.invoice .body {
    padding: 0 20px 12px 20px;
}

.products_container .product_link {
    padding-top: 10px;
    display: block;
}

.product_link .product {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.product_link .product .product_information_part {
    display: flex;
    align-items: center;
}

.product_link .product .product_image {
    width: 90px;
    height: 90px;
    background-position: 50%;
    border: 1px solid rgba(0, 0, 0, 0.1);
}

.product_link .product .product_informations {
    margin-left: 12px;
}

.product .product_informations .product_name {
    font-size: 1.5rem;
    color: var(--text-color);
}

.product_link .product .product_price {
    font-size: 1.5rem;
    color: var(--text-color);
}

.horizontal {
    height: 1px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.invoice_grand_total {
    display: flex;
    justify-content: space-between;
    font-size: 1.7rem;
    color: var(--text-color);
    background-color: #fffefb;
    padding: 35px 20px;
}
.invoice_grand_total .shipping_information .title {
    font-size: 2.2rem;
    text-transform: capitalize; 
    color: rgba(0, 0, 0, 0.8);
}

.invoice_grand_total .shipping_information .info_container {
    font-size: 1.4rem;
    margin-top: 10px;
    color: rgba(0, 0, 0, 0.7);
}

.invoice_grand_total .total_area {
    text-align: right;
}

.invoice_grand_total .total_area .value {
    margin-left: 8px;
    color: var(--primary-color);
}

.invoice_status {
    font-size: 1.6rem;
    font-weight: 600;
    text-transform: uppercase;
}

.invoice_status.completed {
    color: #28a745;
}

.invoice_status.pending {
    color: #ffc107;
}

.invoice_status.delivering {
    color: rgb(1, 118, 195);
}

.invoice_status.canceled {
    color: #ff2a2a;
}

.empty_invoice_list_container {
    height: 600px;
    background-color: var(--white-color);
    border-radius: 2px;
    box-shadow: 0 1px 1px 0 rgb(0 0 0 / 5%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.empty_invoice_list_container .empty_invoice_list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
}
.empty_invoice_list_container .empty_invoice_list .image {
    height: 100px;
}

.empty_invoice_list_container .empty_invoice_list .title {
    font-size: 1.8rem;
    text-align: center;
}