.section_container {
    padding: 20px 0;
}

.container {
    margin-top: 20px;
    min-height: 600px;
}

.empty_search_container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 600px;
}

.empty_search_container .empty_search {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.empty_search_container .empty_search .image {
    width: 150px;
    height: 150px;
}

.empty_search_container .empty_search .title {
    font-size: 2rem;
    text-align: center;
    color: rgba(0, 0, 0, 0.8);
}

