.container {
    background-color: var(--white-color);
    padding: 0 25px 40px 25px;
}

.container .header_container .hero_title {
    font-size: 2rem;
    text-transform: capitalize;
    padding: 25px 0;
}

.container .horizontal {
    height: 1px;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.body_container {
    padding: 25px 0;
}

.user_information_form .text_area {
    display: flex;
    align-items: center;
    width: 100%;
}

.user_information_form .text_area + .text_area {
    margin-top: 30px;
}

.user_information_form .text_area .input_label {
    flex-basis: 110px;
    flex-shrink: 0;
    font-size: 1.5rem;
    text-transform: capitalize;
    color: rgba(85,85,85,.8);
    text-align: right;
}

.user_information_form .text_area .input_wrapper {
    flex-grow: 0;
    width: 100%;
    margin-left: 15px;
    display: flex;
    align-items: center;
}

.user_information_form .text_area .input_wrapper .text_content {
    font-size: 1.5rem;
}

.user_information_form .text_area .input_wrapper.have_input_box {
    box-shadow: inset 0 2px 0 0 rgb(0 0 0 / 2%);
    border: 1px solid rgba(0,0,0,.14);
    padding: 10px;
    border-radius: 2px;
    height: 40px;
}

.user_information_form .text_area .input_wrapper.focus {
    border: 1px solid rgba(0, 0, 0, 0.54);
}
.user_information_form .text_area .input_wrapper .input_date_box,
.user_information_form .text_area .input_wrapper .select_box {
    padding: 2px 5px;
    font-size: 1.4rem;
}

.user_information_form .text_area .input_box {
    border: none;
    outline: none;
    font-size: 1.5rem;
}

.avatar_preview_area {
    padding: 40px 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
}

.avatar_preview_box {
    width: 150px;
    height: 150px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    background-position: 50%;
}

.avatar_input {
    display: none;
}

.form_controller {
    margin-top: 30px;
    text-align: center;
}

.form_controller button {
    padding: 6px 18px;
}