.container {
  padding: 50px 0;
}

.title {
  font-size: 3rem;
  margin-top: 40px;
  text-align: center;
  color: var(--primary-color);
}

.loader {
  border: 16px solid var(--white-color);
  border-top: 16px solid var(--primary-color);
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
  margin: 0 auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
