.container {
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
}

.container .avatar {
    display: flex;
    align-items: center;
}

.container .avatar .avatar_img {
    width: 20px;
    height: 20px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    background-color: var(--white-color);
}

.container .name_wrapper {
    margin-left: 6px;
}

.dropdown {
    position: absolute;
    right: 0;
    top: calc(100% + 12px);
    background-color: var(--white-color);
    color: var(--text-color);
    z-index: 999;
    border-radius: 2px;
    box-shadow: 0 1px 1px 0 rgb(0 0 0 / 5%);
}

.dropdown::after {
    content: "";
    position: absolute;
    top: -24px;
    right: 10px;
    border-width: 12px 20px;
    border-style: solid;
    border-color: transparent transparent var(--white-color) transparent;
}

.nav_list li {
    display: block;
}

.nav_list li .nav_item {
    padding: 10px 15px;
    color: var(--text-color);
    display: block;
    white-space: nowrap;
    text-transform: capitalize;
    font-size: 1.4rem;
}

.nav_list li .nav_item:hover {
    color: var(--primary-color);
    background-color: #fafafa;
}

