.container {
    width: 100%;
}

.container .header_container {
    display: flex;
    align-items: center;
    color: rgba(0, 0, 0, 0.9);
    padding-bottom: 10px;
}

.container .header_container .title{
    font-size: 1.7rem;
    text-transform: uppercase;
    font-weight: 600;
}

.container .header_container .icon {
    margin-right: 8px;
    font-size: 1.4rem;
}

.container .body_container .filter_item {
    padding: 10px 0;
}

.container .body_container .filter_item > button {
    width: 100%;
}

.container .body_container .filter_item + .filter_item {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.body_container .filter_item > .title{
    padding: 10px 0;
    font-size: 1.6rem;
}

.filter_item .category_container .wrapper {
    cursor: pointer;
}

.filter_item .category_container .input_area {
    display: flex;
    align-items: center;
    font-size: 1.4rem;
    gap: 10px;
}

.filter_item .category_container .input_area input[type="checkbox"],
.filter_item .category_container .input_area label {
    cursor: pointer;
}

.body_container .price_filter .price_filter_box {
    display: flex;
    align-items: center;
}

.body_container .price_filter .price_filter_box .horizontal {
    height: 1px;
    background-color: #bdbdbd;
    flex: 1;
    margin: 0 8px;
}

.body_container .price_filter .price_filter_box .input_box {
    width: 42%;
    border-radius: 2px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: inset 0 1px 0 0 rgb(0 0 0 / 5%);
    outline: none;
    height: 30px;
    padding: 0 6px;
}

.price_filter_controller {
    margin-top: 15px;
}

.price_filter_controller button {
    width: 100%;
}

.star_container {
    color: #ffce3d;
    display: flex;
    gap: 4px;
    font-size: 1.4rem;
    cursor: pointer;
    padding: 4px 8px;
}

.star_container .star {
    display: flex;
    align-items: center;
}

.star_container .title {
    display: inline-block;
    color: var(--text-color);
    margin-left: 4px;
}

.star_container + .star_container {
    margin-top: 4px;
}

.star_container.current {
    background-color: #eae5e5;
    border-radius: 5px;
}