.section_container {
    min-height: 100vh;
    padding: 0 0 60px;
}

.container {
    margin-top: 30px;
}

.products_show {
    position: relative;
}

.products_show .main_title {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 60px;
    background-color: var(--white-color);
}

.products_show .main_title .title {
    font-size: 1.8rem;
    text-transform: uppercase;
    color: var(--primary-color);
    padding: 0 12px;
    display: inline-flex;
    height: 100%;
    align-items: center;
    border-bottom: 3px solid var(--primary-color);
}

.products_show .main_content {
    padding-top: calc(60px - 12px);
}

.bottom_controller {
    display: flex;
    justify-content: center;
}

.bottom_button {
    min-width: 24em;
    margin-top: 30px;
}

.product_slider {
    background-color: var(--white-color);
    margin-top: 12px;
    padding: 15px;
}