:root {
    --background-gradient-color: linear-gradient(#f6412e, #ff6633);
    --sidebar-background-gradient-color: linear-gradient(90deg, rgba(71,91,98,1) 0%, rgba(251,251,251,1) 100%);;
    --white-color: #fff;
    --text-color: #000;
    --nav-top-height: 34px;
    --text-size: 1.4rem;
    --body-background-color: #f5f5f5;
    --primary-color: #ee4d2d;
    --header-search-height: 40px;
    --primary-button-color: #FB6445;
    --popup: 998;
    --header: 999;
    --mini-tab: 997;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Source Sans Pro', sans-serif;
}

html {
    font-size: 62.5%;
    line-height: 24px;
}

body {
    background-color: var(--body-background-color);
}

.background-gradient-color {
    background-image: var(--background-gradient-color);
}

.primary-color {
    color: var(--primary-color);
}

.d-flex {
    display: flex;
    width: 100% ;
}

.d-flex-c {
    display: flex;
    align-items: center;
}

.flex-sb {
    justify-content: space-between;
}

.flex-wr-sb {
    flex-wrap: wrap;
    align-items: center;
    height: 100%;
}

a {
    text-decoration: none;
}

ul, li {
    list-style-type: none;
    padding-left: 0;
}

ul li {
    display: inline-block;
}

/* Dropdown */

.has-dropdown {
    position: relative;
}

.dropdown-pane {
    position: absolute;
    display: none;
}

.static-color {
    color: var(--text-color) !important;
}

.btn {
    display: inline-block;
    min-width: 50px;
    text-align: center;
    font-size: 1.5rem;
    background-color: var(--white-color);
    padding: 4px 14px;
    border-radius: 2px;
    color: var(--text-color);
    outline: none;
    cursor: pointer;
    border: 1px solid rgba(0, 0, 0, 0.1);
}

.btn--primary {
    color: var(--white-color);
    background-color: var(--primary-button-color);
    border: none;
}

.btn.disabled {
    user-select: none;
    pointer-events: none;
    background-color: #f7a696;
    border: 1px solid #f7a696;
}

.btn:hover {
    opacity: 0.95;
}

.content-container {
    padding: 30px 0;
    min-height: 400px;
}

input[type="number"]::-webkit-outer-spin-button, 
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
input[type="number"] {
    -moz-appearance: textfield;
}

.quantity_control {
    display: inline-flex;
}

.quantity_control_input,
.quantity_control_button {
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: transparent;
    outline: none;
}

.quantity_control_button {
    width: 32px;
    height: 32px;
    font-size: 1.8rem;
    cursor: pointer;
    border-radius: 3px;
}

.quantity_control_button:first-child {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.quantity_control_button:last-child {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.quantity_control_input {
    border-left: none;
    border-right: none;
    padding: 0 10px;
    width: 50px;
    height: 32px;
    text-align: center;
    font-size: 1.5rem;
}

.section_container {
    min-height: 80vh;
}

table {
    border-collapse: collapse;
}

.text-l {
    text-align: left;
}

.small_flex_column {
    flex: 0.5 1 !important;
}