.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.modal_container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--white-color);
    width: 80%;
    height: 70vh;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
    padding: 20px;
}

.modal_container .modal_header .hero_title {
    font-size: 2.4rem;
}

.modal_container .modal_body {
    margin-top: 30px;
    max-height: calc(130px*4 + 44px);
    overflow-x: hidden;
    overflow-y: scroll;
}

.modal_container .modal_body::-webkit-scrollbar {
    display: none;
}

.table {
    width: 100%;
    border-collapse: collapse;
}

.table thead {
    color: var(--white-color);
    background-color: var(--primary-color);
    height: 44px;
    font-size: 1.4rem;
    font-weight: 600;
}

.table tbody tr {
    height: 130px;
}

.table tbody tr + tr {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.table tbody tr td {
    vertical-align: middle;
    text-align: center;
    font-size: 1.6rem;
}

.product_name {
    display: inline-block;
    max-height: 100px;
    width: 180px;
    overflow-wrap: break-word;
    overflow-y: scroll;
    text-align: left;
}

.product_name::-webkit-scrollbar {
    display: none;
}

.product_image {
    width: 100px;
    height: 100px;
    display: inline-block;
}

.product_controller {
    display: inline-block;
    color: rgb(1, 118, 195);
    font-weight: 600;
    cursor: pointer;
}

.modal_controller {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
}