.container {
    padding: 30px 0;
}

.ProductViewer {
    background-color: var(--white-color);
    border-radius: 3px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    width: 100%;
}

.img_wrapper {
    width: 450px;
    padding: 15px;
    flex-shrink: 0;
}

.img {
    padding-top: 100%;
    background-position: 50%;
    background-size: contain;
    background-repeat: no-repeat;
}

.info_container {
    flex: 1 1 auto;
    padding: 35px;
}

.title {
    font-size: 2.2rem;
    width: 100%;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}   

.rating_wrapper {
    padding: 10px 0;
    display: flex;
    align-items: center;
}

.rating_star {
    color: var(--primary-color);
    font-size: 1.9rem;
    margin-right: 14px;
}

.rating_star .number {
    text-decoration: underline;
}

.rating_star_wrapper {
    display: inline-block;
    margin-left: 6px;
    font-size: 1.5rem;
}

.rating_rate_quantity {
    font-size: 1.4rem;
    margin-right: 14px;
}

.rating_sold_count .number,
.rating_rate_quantity .number {
    font-size: 1.6rem;
    margin-right: 4px;
    text-decoration: underline;
}

.rating_sold_count {
    font-size: 1.4rem;
}


.price_wrapper {
    background-color: var(--body-background-color);
    display: flex;
    padding: 20px;
    align-items: center;
}

.price_base {
    font-size: 1.8rem;
    text-decoration: line-through;
    color: #929292;
    margin-right: 10px;
}

.price_new {
    font-size: 3rem;
    color: var(--primary-color);
}

.price_discount_badge {
    background: var(--primary-color);
    padding: 2px 4px;
    font-size: 1.2rem;
    line-height: 1.3rem;
    color: var(--white-color);
    text-transform: uppercase;
    border-radius: 3px;
    font-weight: 600;
    margin-left: 10px;
}

.product_info_container {
    padding: 20px 0;    
}

.product_info_item {
    font-size: 1.5rem;
    padding: 10px 0;
}

.category_container {
    display: inline-block;
}

.category_container a{
    display: inline-block;
    color: var(--text-color);
    padding: 4px 10px;
    border-radius: 3px;
    border: 1px solid rgba(0, 0, 0, 0.1);
}

.category_container a:hover {
    color: var(--primary-color);
    border: 1px solid var(--primary-color);
}

.product_control_container {
    padding: 20px 0;
}

.product_control__addtocart_button {
    border-radius: 2px;
    border: 1px solid var(--primary-color);
    background-color: rgba(208,1,27,.08);
    padding: 13px 20px;
    font-size: 1.6rem;
    color: var(--primary-color);
    cursor: pointer;
    margin-right: 10px;
}

.product_control__addtocart_button:hover {
    background-color: rgba(241,180,187,.1628);
}

.product_control__buynow_button {
    color: var(--white-color);
    font-size: 1.6rem;
    background-color: var(--primary-color);
    border: 1px solid var(--primary-color);
    border-radius: 2px;
    border: none;
    padding: 13px 20px;
    cursor: pointer;
}

.product_control__buynow_button:hover {
    opacity: 0.9;
}

.label {
    width: 110px;
    display: inline-block;
}

.rating_container,
.detail_container {
    background-color: var(--white-color);
    border-radius: 3px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
    margin-top: 15px;
    padding: 25px;
}

.detail_container .hero_tittle {
    background-color: var(--body-background-color);
    font-size: 2.2rem;
    padding: 15px;
    text-transform: uppercase;
}

.description_container {
    padding: 15px;
}

.detail_item {
    font-size: 1.6rem;
    display: flex;
    align-items: center;
    padding: 6px 0;
}

.detail_item .title {
    width: 140px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: hidden;
}

.detail_item .description {
    color: var(--text-color);
}

.detail_item a {
    color: var(--primary-color);
}


.rating_container .hero_tittle {
    font-size: 2.2rem;
    text-transform: uppercase;
}