.item {
    margin-top: 24px;
    position: relative;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
}

.item_view {
    background-color: var(--white-color);
    width: 100%;
    border-radius: 3px;
    overflow: hidden;
    min-height: 320px;
}

.item_view:hover {
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}

.item_img_wrapper {
    width: 100%;
    padding-top: 100%;
    position: relative;
}

.item_img {
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;
}

.item_info_wrapper {
    padding: 10px;
}

.item_info_name {
    font-size: 1.3rem;
    color: var(--text-color);
    line-height: 1.4rem;
    min-height: 2.8rem;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    word-wrap: break-word;
    overflow: hidden;
    text-overflow: ellipsis;
}

.item_info_price_wrapper {
    padding: 8px 0;
    display: flex;
    align-items: center;
    overflow: hidden;
}

.item_info_price {
    text-overflow: ellipsis;
    overflow: hidden;
}

.item_info_old_price {
    display: block;
    margin-right: 5px;
    flex: 0 1 auto; 
    font-size: 1.5rem;
    color: var(--text-color);
    max-width: 100%;
    text-decoration: line-through;
    line-height: 1.4rem;
}

.item_info_current_price {
    display: block;
    font-size: 1.6rem;
    line-height: 1.75rem;
    flex-grow: 1;
    flex-shrink: 0;
    color: var(--primary-color);
    text-align: right;
}

.item_info_meta_wrapper {
    display: flex;
    justify-content: space-between;
}

.item_info_meta_like {
    flex-shrink: 0;
    margin-right: 4px;
}

.item_info_meta_like > i {
    font-size: 1.3rem;
    width: 16px;
    color: rgba(0, 0, 0, 0.36);
}

.item_info_meta_start_rating {
    display: flex;
    align-items: center;
}

.item_info_meta_start_rating > i {
    font-size: 0.9rem;
    color: rgba(0, 0, 0, 0.36);
}


.item_info_meta_start_rating i +  i {
    margin-left: 2px;
}

.item_rating_icon__active {
    color: #ffce3d !important;
}

.item_info_meta_quantity_sold {
    float: right;
    font-size: 1.2rem;
    color: var(--text-color);
    overflow: hidden;
    text-overflow: ellipsis;
}


.item_info_original {
    margin-top: 4px;
    display: flex;
    justify-content: space-between;
}

.item_info_manufacturer {
    font-size: 1.4rem;
    color: var(--text-color);
}

.item_info_shipment_from {
    font-size: 1.4rem;
    color: var(--text-color);
}

.item .badge {
    position: absolute;
    top: 0;
    right: 0;
}

.badge_container {
    background-color: rgba(255,212,36,.9);
    padding: 3px 4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    text-transform: uppercase;
}

.badge_container::after {
    content: "";
    width: 0;
    height: 0;
    left: 0;
    bottom: -4px;
    position: absolute;
    border-color: transparent rgba(255,212,36,.9);
    border-style: solid;
    border-width: 0 18px 4px;
}

.badge_container .value {
    font-size: 1.4rem;
    line-height: 1.45rem;
    color: #ee4d2d;
}

.badge_container .content {
    font-size: 1.3rem;
    line-height: 1.35rem;
    color: var(--white-color);
    text-transform: uppercase;
}