:root {
    --sidebar-width: 200px;
    --sidebar-background-color: #f2f2f2;
}

.container {
    background-color: var(--sidebar-background-color);
    width: var(--sidebar-width);
    height: 100vh;
    position: fixed;
    border-right: 1px solid rgba(0,0,0,0.1);
    transition: all linear 0.3s;
    z-index: 999;
}

.header_content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 10px 0;
}

.hero_logo,
.hero_title {
    display: inline-block;
}

.hero_title {
    font-size: 2.2rem;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
}

.hero_logo {
    font-size: 1.8rem;
    cursor: pointer;
    padding: 0 4px;
    margin-top: 4px;
}

.body_content {
    padding: 46px 0 8px 0;
    height: calc(100% - (57px + 38px));
}

.nav_list > li{
    display: block;
}

.nav_list .nav_item_link {
    display: flex;
    height: 44px;
    align-items: center;
    padding: 12px 15px;
    color: var(--text-color);
    font-size: 1.65rem;
    text-transform: capitalize;
    white-space: nowrap;
}

.nav_list .nav_item_link .item_name_wrapper {
    flex: 1;
    overflow: hidden
}

.nav_list .nav_item_link:hover {
    background-color: var(--white-color);
}

.nav_list .nav_item_link > i {
    display: inline-block;
    width: 24px;
    margin-right: 8px;
    text-align: center;
}

.footer_content {
    background-color: #e5e5e5;
    padding: 0 4px;
}

/* Hide */

.container.hide {
    width: 60px;
}

.container.hide .header_content {
    justify-content: center;
}

.container.hide .hero_title,
.container.hide .nav_item_link .item_name {
    display: none;
}

.container.hide .nav_item_link {
    position: relative;
}

.container.hide .nav_item_link:hover .item_name {
    position: absolute;
    display: block;
    top: 0;
    bottom: 0;
    left: 100%;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.05);
    line-height: 44px;
    border-radius: 4px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 0 12px;
    background: var(--white-color);
}