.container {
    background-color: var(--white-color);
    padding: 0 25px 40px 25px;
}

.container .header_container .hero_title {
    font-size: 2rem;
    text-transform: capitalize;
    padding: 25px 0;
}

.container .horizontal {
    height: 1px;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.body_container {
    padding: 25px 0;
}

.changePassword_form .text_area {
    display: flex;
    align-items: center;
    width: 100%;
}

.changePassword_form .text_area + .text_area {
    margin-top: 30px;
}

.changePassword_form .text_area .input_label {
    flex-basis: 110px;
    flex-shrink: 0;
    font-size: 1.5rem;
    text-transform: capitalize;
    color: rgba(85,85,85,.8);
    text-align: right;
}

.changePassword_form .text_area .input_wrapper {
    flex-grow: 0;
    width: 100%;
    margin-left: 15px;
    display: flex;
    align-items: center;
}

.changePassword_form .text_area .input_wrapper .text_content {
    font-size: 1.5rem;
}

.changePassword_form .text_area .input_wrapper.have_input_box {
    box-shadow: inset 0 2px 0 0 rgb(0 0 0 / 2%);
    border: 1px solid rgba(0,0,0,.14);
    padding: 10px;
    border-radius: 2px;
    height: 40px;
}

.changePassword_form .text_area .input_wrapper.focus {
    border: 1px solid rgba(0, 0, 0, 0.54);
}
.changePassword_form .text_area .input_wrapper .input_date_box,
.changePassword_form .text_area .input_wrapper .select_box {
    padding: 2px 5px;
    font-size: 1.4rem;
}

.changePassword_form .text_area .input_box {
    border: none;
    outline: none;
    font-size: 1.5rem;
}

.form_controller {
    margin: 30px 0 0 125px;
}

.form_controller button {
    padding: 6px 18px;
}

.errors_area {
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    height: 100%;
    padding: 0 30px;
}

.errors_area .errors_container .errors_item {
    display: block;
    font-size: 1.6rem;
    color: #ff2a2a;
    line-height: 40px;
}

.errors_area .errors_container .errors_item + .errors_item {
    margin-top: 30px;
}