.container {
    padding: 30px 0;
    display: flex;
    justify-content: center;
}

.container ul .item {
    font-size: 2rem;
    display: block;
    padding: 3px 15px;
    color: rgba(0, 0, 0, 0.4);
    margin: 0 12px;
    text-align: center;
    cursor: pointer;
    min-width: 39.95px;
}

.container ul .item.current {
    background-color: var(--primary-color);
    color: var(--white-color);
    border-radius: 3px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.09);
}

.container ul .item:hover {
    color: var(--primary-color);
}

.container ul .item.current:hover {
    color: var(--white-color);
}