.container {
    flex: 1;
    border-radius: 4px;
    box-shadow: 0 0 10px rgb(0 0 0 / 20%);
    padding: 10px;
}

.container .title {
    font-weight: bold;
    color: rgba(0, 0, 0, 0.6);
    font-size: 1.6rem;
    text-transform: uppercase;
}

.container .counter {
    margin: 15px 0;
    font-size: 3rem;
}

.container .bottom_container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.container .bottom_container .link{
    display: block;
    font-size: 1.4rem;
    color: rgba(0, 0, 0, 0.5);
    text-decoration: underline;
}

.container .bottom_container .icon {
    font-size: 2.2rem;
}